@media screen and (max-width: 1200px) {
    .container {
        width: 100%;
    }
}
@media screen and (max-width: 950px) {
    .vlnb-discr,
    .pentest-discr {
        h3 {
            font-size: 25px;
        }
        p {
            font-size: 16px;
        }
        a {
            height: 36px;
            font-size: 18px;
            line-height: 36px;
        }
    }
    .mainMenuPrtLeft {
        flex-direction: row;
        width: 180px;
        p {
            width: auto;
        }
    }
}
@media screen and (max-width: 900px) {
    .bg-hcombs,
    .bgFrst,
    .bgScnd,
    .bgFrd,
    .bgFft,
    .bg-dark {
        height: 100%;
    }
    .loaderBgColor {
        top: 40vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
        width: calc(900px * 0.8);
        background: rgb(0, 136, 136);
    }
    .loaderBgImg {
        top: 40vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
        width: calc(900px * 0.8);
    }
    .loaderBgRed {
        top: 40vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
    }
    @keyframes loaderColor {
        0% {width: calc(150px * 0.8);}
        100% {width: calc(900px * 0.8);}
    }   
    h1 {
        text-align: center;
        width: 90%;
        padding: 0 0 10px 0;
        margin: 30vh auto 0;
        border-left: none;
        border-bottom: 2px solid cyan;
        font-size: 28px;
    }
    h2 {
        text-align: center;
        margin: 40px auto;
        padding: 0;
        width: 90%;
        font-size: 20px;
    }
    .header-content {
        margin: 0;
    }
    .mainMenu {
        a,p {
            font-size: 18px;
        }
    }
    .menuLogoOpen {
        width: 40px;
        height: 40px;
    }
    .mainMenuPrtRight {
        width: 450px;
    }
    .mainMenuPrtLeft {
        flex-direction: row;
        width: 160px;
    }
}



@media screen and (max-width: 750px) {
    .mainMenuPrtLeft {
        width: 20%;
    }
    .mainMenuPrtRight {
        width: 60%;
    }
    .loaderBgColor {
        left: calc(50% - calc(450px * 0.7));
        height: calc(400px * 0.7);
        width: calc(900px * 0.7);
        background: rgb(0, 136, 136);
    }
    .loaderBgImg {
        left: calc(50% - calc(450px * 0.7));
        height: calc(400px * 0.7);
        width: calc(900px * 0.7);
    }
    .loaderBgRed {
        left: calc(50% - calc(450px * 0.7));
        height: calc(400px * 0.7);
    }
    @keyframes loaderColor {
        0% {width: calc(150px * 0.7);}
        100% {width: calc(900px * 0.7);}
    }   
    h1 {
        text-align: center;
        width: 90%;
        padding: 0 0 10px 0;
        border-left: none;
        border-bottom: 2px solid cyan;
        font-size: 28px;
    }
    h2 {
        text-align: center;
        margin: 50px auto 0;
        padding: 0;
        width: 90%;
        font-size: 20px;
    }
    .header-content {
        margin: 0;
    }
    .mainMenu {
        a,p {
            font-size: 18px;
        }
    }
    footer {
        height: 180px;
        h4 {
            font-size: 20px;
        }
        p,a {
            font-size: 17px;
        }
    }
}


@media screen and (max-width: 650px) {
    .about-vlnb,
    .about-pentest {
        flex-direction: column;
    }
    .vlnb-image,
    .vlnb-discr,
    .pentest-image,
    .pentest-discr {
        justify-content: center;
        align-items: center;
        width: 100%;
        h3,a,p {
            text-align: center;
        }
    }
}
@media screen and (max-width: 600px) {
    .menuLogoOpen {
        display: block;
    }
    h1 {
        font-size: 24px;
    }
    h2 {
        font-size: 16px;
    }
    .loaderBgColor {
        left: calc(50% - calc(450px * 0.6));
        height: calc(400px * 0.6);
        width: calc(900px * 0.6);
        background: rgb(0, 136, 136);
    }
    .loaderBgImg {
        left: calc(50% - calc(450px * 0.6));
        height: calc(400px * 0.6);
        width: calc(900px * 0.6);
    }
    .loaderBgRed {
        left: calc(50% - calc(450px * 0.6));
        height: calc(400px * 0.6);
    }
    @keyframes loaderColor {
        0% {width: calc(150px * 0.6);}
        100% {width: calc(900px * 0.6);}
    }  
    .mainMenuPrtRight {
        width: 0;
        a,p {
            width: 0;
        }
    }
    .mainMenuPrtLeft {
        width: calc(50% + 25px);
        flex-direction: row-reverse;
        justify-content: space-between;
    }
}


